<template>
  <div class="page has-navbar" v-nav="{title: '',showBackButton: true}">
    <div class="page-content padding-top">

      <swiper class="swiperStyle":options="swiperOption" ref="mySwiper">
         <!-- slides -->

           <div class="swiper-slide"    v-for="info in machineInfo">
             <machine_cmp  v-bind:totalInfo='info'></machine_cmp>
           </div>

         <!-- Optional controls -->

         <div class="swiper-scrollbar"   slot="scrollbar"></div>
       </swiper>

    </div>


  </div>
</template>

<script>
import machine_cmp from './machine_cmp.vue'
import { swiper, swiperSlide } from 'vue-awesome-swiper'
  export default {
    data() {
      return {
        swiperOption: {
          notNextTick: true,

          preventClicks : false,

          preventLinksPropagation : true,
          slidesPerView: 1.2,
          centeredSlides: true,
          spaceBetween: 20,
          parallax: true,// 
          pagination: {
            el: '.swiper-pagination',//
            clickable: true,
          },
            zoom: {
            toggle: false,
          },
          // effect : 'cube',
          // cubeEffect: {
          //   slideShadows: true,
          //   shadow: true,
          //   shadowOffset: 100,
          //   shadowScale: 0.6
          // },
        },
        machineInfo: [
        { id: 1, title: '1',address: '',status: ""},
        { id: 2, title: '2',address: 'CoCoPark' ,status: ""},
        { id: 3, title: '3',address: '',status: "" }
        ],
      }
    },

    components: {
    swiper,
    swiperSlide,
    machine_cmp
  },
  methods:{
    aa(){
      return this.posts
    },

    showPopup() {
      let options = {
        effect: 'scale',
        title: 'aaa',
        buttons: [
          {text: ''},
          {text: ''},
        ]
      }

      let popup = $popup.fromTemplate('<p style="margin-bottom: 0; text-align: center;"></p>', options)

      popup.show().then((buttonIndex) => {
        console.log(buttonIndex)
      })
    }
  },
    // you can find current swiper instance object like this, while the notNextTick property value must be true
    // swiperswipernotNextTicktrue
    computed: {
      swiper() {
        return this.$refs.mySwiper.swiper
      }
    },
    mounted() {
      // you can use current swiper instance object to do something(swiper methods)
      // swiper
      // console.log('this is current swiper instance object', this.swiper)
      // this.swiper.slideTo(3, 1000, false)
    }
  }

</script>




<style scoped>
.swiperStyle{
  margin-top: 25px;
}
.machine_control{
  background-color: white;
}

.machine_button{
  margin: 2%;
  float: left;
  width: 46%;
}

.machine_address{
  font-size: small;
}
.des_title{
  padding: 15px;
  margin: 0px 15px;
  border-bottom: 2px solid gray;
}
.machine_des{
    text-align: center;
    width: 100%;
    max-height: 100%;
    margin: 20px auto;
    background-color: white;
}
</style>



