<template>
	<div class="page has-navbar" v-nav="{title: '',showBackButton: true}">
		<div class="page-content padding-top">
			<div class="item item-divider">
				
			</div>
			<cascade :fields="fields" :data="cities" :value="value" :on-change="onChange"></cascade>

		</div>
	</div>
</template>

<script>
	export default{
		data() {
			return {
				fields:[
          			"", "", ""
				],
				cities: [
		          ['', '', ''],
		          ['', '', 'asd'],
		          ['', 'aa', 'bb'],
		          // ...
		        ],
		        value: []
			}
		},
		methods: {
			onChange(value){
				this.value = value;
				console.log(this.value);
			}
		}
	}
</script>


