<template>
  <div class="page has-navbar" v-nav="{title: ''}">


    <div class="page-content">
        <tabs :tab-items="tabs" :tab-index="tabIndex" :on-tab-click="onTabClick">

        </tabs>
        <div class="identified_style"  v-if="tabIndex==0">
          <von-input
            type="text"
            v-model="single_name"
            placeholder=""
            label=":"
            >
          </von-input>
          <von-input
            type="text"
            v-model="single_identity"
            placeholder=""
            label=":"
            >
          </von-input>
          <von-input
            type="text"
            v-model="single_address"
            placeholder=""
            label=":"
            >
          </von-input>
          <von-input
            type="text"
            v-model="single_account"
            placeholder=""
            label=":"
            >
          </von-input>
          <md-button class="button button-balanced" @click.native="change_machine_info"></md-button>

        </div>
        <div class="identified_style" v-else-if="tabIndex==1">
          <von-input
            type="text"
            v-model="company_name"
            placeholder=""
            label=":"
            >
          </von-input>
          <von-input
            type="text"
            v-model="company_legalname"
            placeholder=""
            label=":"
            >
          </von-input>
          <von-input
            type="text"
            v-model="company_license"
            placeholder=""
            label=":"
            >
          </von-input>
          <von-input
            type="text"
            v-model="company_code"
            placeholder=""
            label=":"
            >
          </von-input>
          <von-input
            type="text"
            v-model="company_account"
            placeholder=""
            label=":"
            >
          </von-input>
          <md-button class="button button-balanced" @click.native="change_machine_info"></md-button>

        </div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return{
        tabs: [
        "",
        "",
        ],
        tabIndex: 0,
        single_name: '',
        single_identity: '',
        single_address: '',
        single_account: '',
        company_name: '',
        company_account: '',
        company_code: '',
        company_license: '',
        company_legalname: '',



      }
    },
    methods: {
      onTabClick(index) {
        this.tabIndex = index
      },
      change_machine_info(){
        $toast.show('', 700).then(() => {
          var t = this;
          t.$emit('test', 'hi');
      })
      }
    }
  }
</script>
<style scoped>
.button{
  margin: 5%;
  width: 90%;
}
.identified_style{
    margin-top: 50px;
  }
</style>



